html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}
body {
  line-height: 1.6;
  font-family: -apple-system-font, "Helvetica Neue", sans-serif;
}
* {
  margin: 0;
  padding: 0;
}
a img {
  border: 0;
}
a {
  text-decoration: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
/** env = windows **/
::-webkit-input-placeholder {
  font-family: -apple-system-font, "Helvetica Neue", sans-serif;
}
/** prevent default menu callout **/
a {
  -webkit-touch-callout: none;
}
.panel {
  margin-top: 48px;
  padding-bottom: 48px;
  background-color: #ffffff;
}
.panel .null {
  height: 13px;
  background-color: #f3f3f3;
}
body {
  margin: 0;
}
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  /* margin-top: 60px; */
}

.login-back {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-color: #2a3342;
}
.login-back .logo {
  width: 91px;
  height: 45px;
  margin-top: 42px;
}
.login-back .login-title {
  font-size: 16px;
  color: #ffffff;
  margin-top: 16px;
}
.login-back .login-input-panel {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: flex-start;
  align-items: center;
  width: 260px;
  height: 35px;
  border-radius: 18px;
  border: 1px solid #777777;
}
.login-back .login-input-panel.up {
  margin-top: 38px;
}
.login-back .login-input-panel.down {
  margin-top: 18px;
  margin-bottom: 12px;
}
.login-back .login-input-panel img {
  width: 24px;
  height: 24px;
  margin-left: 20px;
}
.login-back .login-input-panel .login-input {
  border: none;
  background-color: #2b3441;
  width: 180px;
  height: 35px;
  margin-left: 12px;
  color: #777777;
}
.login-back .login-check {
  margin-left: -176px;
  margin-bottom: 8px;
}
.login-back .login-check .weui-icon-success {
  font-size: 18px;
  color: #00c2e0;
}
.login-back .login-check .weui-icon-success-circle {
  font-size: 18px;
  color: #00c2e0;
}
.login-back .login-check .weui-icon-success-circle:before {
  color: #00c2e0;
}
.login-back .login-check .weui-icon-circle {
  font-size: 18px;
  color: #777777;
}
.login-back .login-check span {
  font-size: 14px;
  line-height: 14px;
  color: #777777;
  vertical-align: middle;
}
.login-back .login-sublim {
  width: 260px;
  height: 35px;
  background-color: #00c2e0;
  border: none;
  border-radius: 18px;
  color: #ffffff;
  font-size: 14px;
}
.login-back .login-foot {
  font-size: 12px;
  color: #c1c1c1;
  margin-top: 154px;
}
